.form {
  position: relative;
  padding: 10px;
  margin-bottom: 10px;
  justify-content: space-between;
  box-shadow: 0 2px 1px var(--shadow);
  border-radius: 10px;
  /* border: 1px solid green; */
}

.form .btn__submit {
  position: absolute;
  top: 26px;
  right: 15px;
  z-index: 2;
  transform: translateX(3px);
  height: 26px;
  /* border: 1px solid green; */
}

.form .btn__clear {
  position: absolute;
  top: 26px;
  right: 15px;
  z-index: 3;
  transform: translateX(-20px);
  height: 26px;
  /* border: 1px solid green; */
}
